import './BookingSection.css';
import React, { useState, useEffect } from 'react'; 
import { differenceInCalendarDays } from 'date-fns';
import theChangableBase from'../base.js';
import SuccessPopup from './SuccessPopup.js';
import { Calendar,DateRangePicker } from 'react-date-range';
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file

const BookingSection = () => {
  const [checkIn, setCheckIn] = useState(new Date());
  const [checkOut, setCheckOut] = useState(new Date());
  const [fetchDates, setFetchDates] = useState({});
  
  const [numOfGuests, setNumOfGuests] = useState(0);
  const [kidsOfGuests, setKidsOfGuests] = useState(0);
  const [fullName, setFullName] = useState('');
  const [phoneNum, setPhoneNum] = useState('');
  const [email, setEmail] = useState('');
  const numberOfNights = differenceInCalendarDays(new Date(checkOut), new Date(checkIn));
  const [minDate,setMinDate]=useState('');
  const [disabledDates,setDisabledDates]=useState([]);
  const [calendarOpen, setCalendarOpen] = useState(false);
  const [fetchPricesAndFee, setPricesAndFee] = useState([]);
  const[price,setPrice]=useState("");
  const[fee,setFee]=useState("");
  const [showSuccessPopup, setShowSuccessPopup] = useState(false);
 //fetch data from airtable//
  useEffect(() => {
    const fetchData = async () => {
    const allRecordsOfDates = [];

    await theChangableBase('Carlendar Notes')
      .select({ view: 'Grid view' })
      .eachPage(
        (records, fetchNextPage) => {
           allRecordsOfDates.push(...records);
          fetchNextPage();
        },
        async (err) => {
          if (err) {
            console.error('Error fetching records:', err);
          } else {
            setFetchDates( allRecordsOfDates);
            console.log("check the fetched dates:",fetchDates);
          }
        }
      );
     const allRecordsOfPricesAndFee = [];
     await theChangableBase('PriceAndFee')
        .select({ view: 'Grid view' })
        .eachPage(
          (records, fetchNextPage) => {
           allRecordsOfPricesAndFee.push(...records);
            fetchNextPage();
          },
          async (err) => {
            if (err) {
              console.error('Error fetching records from Price:', err);
            } else {
             
              setPricesAndFee(allRecordsOfPricesAndFee[0]);
              setPrice(allRecordsOfPricesAndFee[0].fields.Price);
              setFee(allRecordsOfPricesAndFee[0].fields.CleaningFee);
              //console.log("the price is",price);
              //console.log("the cleaning fee is",fee);
              //console.log("the price and fee",fetchPricesAndFee);
             
            }
          }
        ); 
      
   };
  fetchData();
  }, []);

 useEffect(() => {
	  if (Array.isArray(fetchDates)) {
	     const timeZone = "Europe/Berlin"; // Update this with the correct time zone
	     const updatedDisabledDates = fetchDates.map(record => {
	     const bookedDate = new Date(record.fields["booked"]);
	     const options = { timeZone };
	     return new Date(bookedDate.toLocaleDateString(undefined, options));
	    });

	    setDisabledDates(updatedDisabledDates);
	  }
	}, [fetchDates]);
 
 
 
  const selectionRange = {
      startDate:checkIn, 
      endDate:checkOut,
      key: 'selection',
    }
 
 ///////////////////////hande calendar////
  const handleDateClick = () => {
    setCalendarOpen(!calendarOpen);
   };
  const handleSelect = (ranges) => { 
     setCheckIn(ranges.selection.startDate);  
       //minDate is 4
     const newEndDate = new Date(ranges.selection.startDate);
     newEndDate.setDate(ranges.selection.startDate.getDate() + 4);
     setMinDate(newEndDate);	
    
     if( newEndDate > ranges.selection.endDate){
        setCheckOut(newEndDate);  
     }else{
    
       setCheckOut(ranges.selection.endDate);
    }
     
     // for open the calendar 
     if (ranges.selection.startDate && ranges.selection.endDate && ranges.selection.startDate !==  ranges.selection.endDate) {
         setCalendarOpen(prevCalendarOpen => !prevCalendarOpen);
  }
   //check if the isabledDates over lap with range
   const isOverlap = disabledDates.some((date) => {  
      return date > checkIn && date < checkOut;
      
    });

    if (isOverlap) {
      alert('Selected range overlaps with unavlailbel dates. Please choose a different range.');
      return;
    }  
    
  };
 
  function CalendarSelect(date){
    console.log(date); 
  }

  /////////////////////////////////////////

  const handleBookingSubmit = async (e) => {
    e.preventDefault();

    const formData = {
      "fields": {
      "Start Date": checkIn.toLocaleDateString(),
      "End Date": checkOut.toLocaleDateString(),
      "Number Of Guests": parseInt(numOfGuests),
      "Number Of Kids": parseInt(kidsOfGuests),
      "Full Name": fullName,
      "Email": email,
      "Phone Number": phoneNum,
    }
    };
   
    fetch(`https://api.airtable.com/v0/appPrmFJzdWiFnCpN/ReservationInfo`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer patjcE0Oa54kfWtFE.820c3b5107a690a0160fa2730a0d1a7354c09bdecad6625f180f671b19e2c4e2',
      },
      body: JSON.stringify(formData),
    })
      .then(response => response.json())
      .then(data => {
        console.log(data);
        //alert('Booking form submitted successfully!');
        setShowSuccessPopup(true);
        // You can redirect the user or perform other actions here
      })
      .catch(error => {
        console.error('Error:', error);
        console.log("hi there is the formData",formData);
        
        alert('Error submitting the booking form. Please try again.');
      });
  };
  
  return (
  
  <div id="booking" className="bookingSection">
  <h2 > Booking </h2>
  <div className="bookingContent">
    <div className="leftPart"> 
        <Calendar
        date={new Date()}
        onChange={CalendarSelect}
        disabledDates={disabledDates}	
	months={1}
	rangeColors={['#FF5A5F']}
	minDate={ new Date()}
	className="calendarLeft"
      />
    </div>
    <form onSubmit={handleBookingSubmit} className="rightPart">
     <div className="formWidth">
      <div className="rightPart--title">€{price} per night</div>
      <div style={{width:"100%"}}>
        <div className="rightPart--dateChooseBox" >
          {calendarOpen && (<DateRangePicker
			ranges={[selectionRange]}
			onChange={handleSelect}
			disabledDates={disabledDates}
			months={1}
			rangeColors={['#FF5A5F']}
			minDate={ new Date()}
			className="absolute-calendar"/>)}
          <div className="rightPart--checkInBox"onClick={handleDateClick} >
            <div>Check in:</div>
             <div> {checkIn && (
               <div className="dateInputbox">{checkIn.toLocaleDateString()} </div> )}
             </div>
          </div>
          <div className="rightPart--checkOutBox" onClick={handleDateClick}>
            <div>Check out:</div>
          <div className="dateInputbox">{checkOut && (
            <div >{checkOut.toLocaleDateString()}</div>)}
          </div>
          </div>
         </div>
       <div className="rightPart--formPadding">
        <div className="form-style">
          <label>Number of guests:
           <input type="number"
	        value={numOfGuests}
	        id="Number of guests"
	        name="Number of guests"
	        onChange={(e) => setNumOfGuests(e.target.value)}
	        min="1" 
	        required/>
	     </label>
        </div>
          { checkIn && checkOut &&numberOfNights >= 4 &&(
         <div>
         <div className="form-style">
           
	   <label>Number of kids:
	   <input type="number"
	         value={kidsOfGuests}
	         id="Number of kids"
	         min="0" 
	         name="Number of children"
	         onChange={(e) => setKidsOfGuests(e.target.value)}
	         required/>
	    </label>
        </div>
	<div className="form-style">
	   <label>Your full name:
	   <input type="text"
	         value={fullName}
	         id="fullName"
	         name="Full name"
	         onChange={(e) => setFullName(e.target.value)}
	         required/></label>
	</div>
	 
	 <div className="form-style">
	   <label>Your email:
	   <input type="email"
	         name="Email"
	          id="bookingEmail"
	         value={email}
	         onChange={(e) => setEmail(e.target.value)}
	         required/></label>
	 </div>
	 
	 <div className="form-style">
	   <label>Your phone number:
	   <input type="text"
                  id="BookingPhone"
	         name="Phone number"
	         value={phoneNum}
	          onChange={(e) => setPhoneNum(e.target.value)}
	         required/></label>
	 </div>
	</div>)
       }
      </div>
     
       <button className="contactButton btn btn-primary " type="submit" 
               onClick={() => setShowSuccessPopup(true)}>
            Reserve
        </button>
        
      { checkIn && checkOut &&numberOfNights >= 4&&(
            <div className="forSummary">
            <p>You won't be charged yet</p>
            <div className="summary--section">
              <span> €{price} x {numberOfNights} nights</span>
              <span>€{(Number(price) * Number(numberOfNights)).toFixed(2)}</span>
            </div>
            { numberOfNights > 0 &&(
            <div className="summary--section">
              <span>Cleaning fee</span>
              <span>€{fee}</span>
            </div>
            )}
            <div className="summary--section totalPrice">
              <span>Total before taxes</span>
               { numberOfNights > 0 ?
              <span>€{((Number(price) * Number(numberOfNights)) + Number(fee)).toFixed(2)}
</span>:<span>€0</span>}
            </div>
          </div>
   )}
  </div>
  </div>
  
    </form>
 {showSuccessPopup && numOfGuests&&fullName&&phoneNum&&email&&(
        <SuccessPopup onClose={() => setShowSuccessPopup(false)}
          numOfGuests={numOfGuests}
          kidsOfGuests={kidsOfGuests}
          fullName={fullName}
          phoneNum={phoneNum}
          email={email}
          checkIn={checkIn.toISOString()} 
          checkOut={checkOut.toISOString()}/>
      )}
    </div>
    <p>*Some notes you should know before you make a reservation:
    You must select at least 5 days.
    Once the host receives your reservation, they will call you back to have a friendly conversation before you proceed with the purchase.</p>
     
  </div>
  );
};

 
export default BookingSection;
